import React, { useState, useEffect } from 'react';
import { Box, Paper, TextField, InputAdornment, IconButton, List, ListItem } from '@mui/material';
import { EditInput, AdicionarNovoInput, BotaoSalvar, Titulo, ValorTotal, AddInput } from '../Complementos';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';

const styles = {
	boxPai: {
		width: '99%',
		height: '75%',
		mb: 1,
		ml: 1,
		mr: 1,
		bgcolor: '#252724',
		borderRadius: '10px'
	},
	container: {
		display: 'flex',
		flexDirection: 'column',
		justifyContent: 'space-around',
		height: '90%',
	},
	boxInputs: {
		display: 'flex',
		flexDirection: 'row',
		flexWrap: 'wrap',
		width: '100%',
	},
	paperInputs: {
		p: 1.3,
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		width: 400
	}
}

const Ganhos = () => {
	const arrayTeste = [
		{ label: 'salario', valor: '' },
		{ label: 'naConta', valor: '' },
		{ label: 'Decimo13', valor: '' },
		{ label: 'outros', valor: '' },
		{ label: 'outros2', valor: '' }
	];

	const [valores, setValores] = useState(arrayTeste);
	const [saldoTotal, setSaldoTotal] = useState('');

	const [dadosParaEditarLabel, setDadosParaEditarLabel] = useState({
		open: false,
		tipo: '',
		labelAtual: '',
		indexAtual: '',
		novoLabel: ''
	})
	const [dadosParaAddInput, setDadosParaAddInput] = useState({
		open: false,
		label: '',
		valor: ''
	})

	const handleValores = (event, index) => {
		const newValue = event.target.value.replace(/[^0-9.]/g, '');
		const updatedValores = [...valores];
		updatedValores[index].valor = newValue;
		setValores(updatedValores);
	};

	const salvaDados = () => {
		console.log(saldoTotal);
	}
	const abreAddNovoInput = () => setDadosParaAddInput({open: true})

	const addValoresNovoInput = (value, tipo) => {
		if(tipo === 'label'){
			setDadosParaAddInput((prevState) => ({
				...prevState,
				label: value
			  }));

		}else{
			setDadosParaAddInput((prevState) => ({
				...prevState,
				valor: value
			  }));
		}
	}

	const salvaNovoInput = () => {
		valores.push({label: dadosParaAddInput.label, valor: dadosParaAddInput.valor}) 
		fechaAddNovoInput();
		console.log('ME CLICOU', dadosParaAddInput)
	}

	const fechaAddNovoInput = () => {
		setDadosParaAddInput({open: false}) 
		console.log(valores)
	}
	

	/* AQUI COMEA A PARTE RESPONSVEL POR EDITAR O LABEL */
	const AbreEditorLabel = (label, index, tipo) => setDadosParaEditarLabel({open: true, tipo: tipo, labelAtual: label, indexAtual: index})

	const NovoLabel = (value) => {
		setDadosParaEditarLabel((prevState) => ({
			...prevState,
			novoLabel: value
		  }));
	}
	const EditaLabel = () => {
		const updatedValores = [...valores];
		updatedValores[dadosParaEditarLabel.indexAtual].label = dadosParaEditarLabel.novoLabel;
		setValores(updatedValores);
		fechaEditorLabel();
	}

	const fechaEditorLabel = () => setDadosParaEditarLabel({open: false})
	
	/* AQUI TERMINA A PARTE RESPONSVEL POR EDITAR O LABEL */

	useEffect(() => {
		const somaValores = valores.reduce((total, item) => total + parseFloat(item.valor || 0), 0);
		setSaldoTotal(somaValores);
	}, [valores]);

	const teste = () => {
		console.log('Opa')
	}
	return (
		<>
		<Box sx={styles.boxPai}>
			<Titulo texto={'Informe aqui os seus ganhos mensais'} />
			<Box sx={styles.container}>

				<List sx={styles.boxInputs}>
					{valores.map((item, index) => (
						<GanhoItem
							key={index}
							item={item}
							index={index}
							handleValores={handleValores}
							AbreEditorLabel={AbreEditorLabel }
						/>
					))}
				</List>

				<Box sx={{ alignSelf: 'self-end', p: 1, mr: 1 }}>

					<AdicionarNovoInput title={"Adicionar novos Ganhos"} click={abreAddNovoInput} />
					<ValorTotal saldoTotal={saldoTotal} />

				</Box>

				<BotaoSalvar texto={'Salvar seus ganhos mensais'} salvaDados={salvaDados} />
			</Box>
		</Box>

			<EditInput 
				title={'Edite o Label'} 
				dadosParaEditarLabel={dadosParaEditarLabel} 
				fechaEditorLabel={fechaEditorLabel}
				EditaLabel={EditaLabel}
				NovoLabel={NovoLabel}
			/>

			<AddInput
				title={'Adicione um novo input'}
				dadosParaAddInput={dadosParaAddInput}
				addValoresNovoInput={addValoresNovoInput}
				salvaNovoInput={salvaNovoInput}
				fechaAddNovoInput={fechaAddNovoInput}
			/>

		</>		
	);
}

const GanhoItem = ({ item, index, handleValores, AbreEditorLabel }) => {
	return (
		<ListItem sx={styles.paperInputs}>
			<Paper elevation={8} component="form" sx={styles.paperInputs}>
				<TextField
					fullWidth
					id={index.toString()}
					label={item.label}
					type="number"
					value={item.valor}
					onChange={(e) => handleValores(e, index)}
					InputProps={{
						startAdornment: <InputAdornment position="start">R$</InputAdornment>,
						inputProps: {
							min: 0
						},
					}}
				/>
				<IconButton color="primary" aria-label="edit" onClick={() => AbreEditorLabel(item.label, index, 'edit')}>
					<EditIcon />
				</IconButton>

				<IconButton color="error" aria-label="delete">
					<DeleteIcon />
				</IconButton>

				{/* <IconButton color="success" aria-label="check">
					<CheckBoxIcon />
				</IconButton> */}
			</Paper>
		</ListItem>
	);
};

export default Ganhos;
